import { Router } from 'itty-router';
import mongoose from 'mongoose';

// 创建路由器
const router = Router();

// 连接到MongoDB
const connectDB = async (env) => {
  try {
    if (!mongoose.connection.readyState) {
      await mongoose.connect(env.MONGODB_URI);
      console.log('MongoDB connected successfully');
    }
  } catch (error) {
    console.error('MongoDB connection error:', error);
    throw error;
  }
};

// 验证JWT token
const verifyToken = async (request, env) => {
  const authHeader = request.headers.get('Authorization');
  if (!authHeader || !authHeader.startsWith('Bearer ')) {
    throw new Error('未提供认证token');
  }

  const token = authHeader.split(' ')[1];
  try {
    // 在这里添加JWT验证逻辑
    // const decoded = jwt.verify(token, env.JWT_SECRET);
    // return decoded;
    return { _id: 'test-user-id' }; // 临时返回测试用户ID
  } catch (error) {
    throw new Error('无效的token');
  }
};

// 基础路由
router.get('/', () => new Response('QAPlanet API is running'));

// API路由
router.all('/api/*', async (request, env) => {
  // 处理CORS
  if (request.method === 'OPTIONS') {
    return new Response(null, {
      headers: {
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Methods': 'GET, POST, PUT, DELETE, OPTIONS',
        'Access-Control-Allow-Headers': 'Content-Type, Authorization',
      },
    });
  }

  // 确保数据库连接
  await connectDB(env);

  const url = new URL(request.url);
  const path = url.pathname;

  try {
    // 用户相关路由
    if (path.startsWith('/api/users')) {
      if (path === '/api/users/register' && request.method === 'POST') {
        const data = await request.json();
        // 处理用户注册
        return new Response(JSON.stringify({ success: true }), {
          headers: { 'Content-Type': 'application/json' }
        });
      }

      if (path === '/api/users/login' && request.method === 'POST') {
        const data = await request.json();
        // 处理用户登录
        return new Response(JSON.stringify({ token: 'test-token' }), {
          headers: { 'Content-Type': 'application/json' }
        });
      }
    }

    // 问答相关路由
    if (path.startsWith('/api/qa')) {
      if (path === '/api/qa/questions' && request.method === 'GET') {
        // 获取问题列表
        return new Response(JSON.stringify({ questions: [] }), {
          headers: { 'Content-Type': 'application/json' }
        });
      }

      if (path === '/api/qa/questions' && request.method === 'POST') {
        // 验证用户token
        const user = await verifyToken(request, env);
        const data = await request.json();
        // 创建新问题
        return new Response(JSON.stringify({ success: true }), {
          headers: { 'Content-Type': 'application/json' }
        });
      }

      if (path.match(/^\/api\/qa\/questions\/\w+$/) && request.method === 'GET') {
        // 获取单个问题详情
        const id = path.split('/').pop();
        return new Response(JSON.stringify({ question: { id } }), {
          headers: { 'Content-Type': 'application/json' }
        });
      }
    }

    // 如果没有匹配的路由
    return new Response('API endpoint not found', {
      status: 404,
      headers: { 'Content-Type': 'application/json' }
    });
  } catch (error) {
    return new Response(JSON.stringify({ error: error.message }), {
      status: 500,
      headers: { 'Content-Type': 'application/json' }
    });
  }
});

// 处理静态文件
router.get('*', async (request, env) => {
  try {
    const url = new URL(request.url);
    let path = url.pathname;
    
    // 默认页面
    if (path === '/') {
      path = '/index.html';
    }

    // 从KV或R2获取静态文件
    // TODO: 实现静态文件服务
    return new Response('Static file not found', { status: 404 });
  } catch (error) {
    return new Response('Error serving static file', { status: 500 });
  }
});

// 导出fetch处理函数
export default {
  async fetch(request, env, ctx) {
    try {
      // 添加CORS头
      const response = await router.handle(request, env);
      response.headers.set('Access-Control-Allow-Origin', '*');
      return response;
    } catch (error) {
      return new Response(JSON.stringify({ error: error.message }), {
        status: 500,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
        },
      });
    }
  },
}; 